
.message_rows{
  display: flex;
}
.columns{
  position: relative; 
  flex:0 0 283px;
  padding: 20px;
}
.columns:last-child{
  height: 525px;
  overflow: auto;
}
.columns:first-child::before{
  content: '';
  position: absolute;
  right: 0;
  width: 1px;
  height: 97%;
  border-right: 1px solid black;
  z-index: 2;
}
.message_name,.message_val{
  margin: 0 0 10px 0;
}
.message_val{
  margin: 0 0 10px 40px;
}
.message_name{
  display: flex;
  align-items: center;
}
.message_link{
  text-decoration: none;
  color: #000;
}
/* .active{
  font-size: 21px;
} */
.message_img{
  margin: 0 10px 0 0;
}
.message_img img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.button_send{
  display: inline-block;
  cursor: pointer;
  height: 30px;
  line-height: 27px;
  padding: 0 20px;

  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background: transparent;
}
.message_textarea{
  width: 320px;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.message_form{
 transform: translate(340px,-70px);
}



